import axios from 'axios';
import { decryptData } from './encrypt';
// import { routeNames } from '../../js/routeNames';

export default function interceptorSetup () {
    axios.interceptors.request.use(function(config) {
      //Decrypt Token
      let mkLocalData = localStorage.getItem('token');
      if(!mkLocalData){
         mkLocalData = 'null';
      }
      const secretTokenKey = process.env.KEY || 'myscecretkey';
      const token = decryptData(mkLocalData, secretTokenKey);

      if(token) {
          config.headers.Authorization = `Bearer ${token}`;
      }
      return config;
    });

    axios.interceptors.response.use(
        response => response,
        error => {
          // Error
          const { config, response: { status } } = error;
      
          if (status === 401) {
            // Unauthorized request: maybe access token has expired!
            //return refreshAccessToken(config);
            console.log(config);
            console.log('Caught the session error');
            localStorage.removeItem('Login');
            axios.post('https://theportal.scala-apac.org/HR_Engine/api/logout', null)
            .then(() =>{
                this.$router.push({ path: '/'});
             }).catch((error) =>{
                this.errors = error.response.data.errors;
            }).finally(() => {  
                localStorage.removeItem('token');
                localStorage.removeItem('Permissions');
                localStorage.removeItem('Email');
                localStorage.removeItem('Role');
                localStorage.removeItem('EmployeeID');
                localStorage.removeItem('PasswordFlag');
                localStorage.removeItem('celebrationGIF');
                location.reload();
            });
          } else {
            return Promise.reject(error);
          }
        });
}