<template>
  <div class="navigation" :class="{ 'nav-opened': navOpen }" v-if="force_change_password">
    <div class="navigation-shell d-flex flex-column justify-content-between">
      <div>
        <div class="navigation-button menu-button"  @click="toggleNav()">
          <div class="copy" id="title">
            <span class="">The Portal</span>
          </div>
          <div class="icon menu" style="color: #fff">
            <!-- <span v-if="navOpen" class="mdi mdi-backburger"></span>
            <span v-else class="mdi mdi-menu"></span> -->
            <!-- <img src="../assets/team.png" height="30" width="45" alt=""> -->
            <img  :src="newIcon" class="new-icon" alt="">
          </div>
        </div>
        <div>
          <div class="nav-holder" v-if="this.permissions.includes('View-User')">
            <NavigationButton
              title="Manage User"
              @mouseover="getPos('user')"
              @mouseout='removePopUp()'
              :routeName="routeNames.manageUser"
              :icon="'mdi-account-multiple-plus'"
              @click.enter="goToRoute(routeNames.manageUser)"
            />
          </div>
          <div class="nav-holder" v-if="this.permissions.includes('View-Role')">
            <NavigationButton
              title="Manage Role"
              class="icons"
              @mouseover="getPos('role')"
              @mouseout='removePopUp()'
              :routeName="routeNames.manageRole"
              :icon="'mdi-account-cog'"
              @click="goToRoute(routeNames.manageRole)"
            />
          </div>
          <div class="nav-holder" v-if="this.permissions.includes('Viewer')">
            <NavigationButton
              class="icons"
              title="Home"
              @mouseover="getPos('home')"
              @mouseout='removePopUp()'
              :icon="'mdi-card-account-details-outline'"
              :routeName="routeNames.home"
              @click="goToRoute(routeNames.home)"
            />
          </div>
          <div class="nav-holder" v-if="this.permissions.includes('Viewer')">
            <NavigationButton
              class="icons"
              title="My Info"
              @mouseover="getPos('info')"
              @mouseout='removePopUp()'
              :routeName="routeNames.myInfo"
              :icon="'mdi-information-outline'"
              @click="goToRoute(routeNames.myInfo)"
            />
          </div>
          <div class="nav-holder" v-if="this.permissions.includes('Viewer')">
            <NavigationButton
              title="People"
              class="icons"
              @mouseover="getPos('people')"
              @mouseout='removePopUp()'
              :routeName="routeNames.People"
              :icon="'mdi-account-group'"
              @click="goToRoute(routeNames.People)"
            />
          </div>
          <div class="nav-holder" v-if="this.permissions.includes('View-Audit')">
            <NavigationButton
              title="Reports"
              class="icons"
              @mouseover="getPos('report')"
              @mouseout='removePopUp()'
              :routeName="routeNames.report"
              :icon="'mdi-file-chart'"
              @click="goToRoute(routeNames.report)"
            />
          </div>
          <div class="nav-holder" v-if="this.permissions.includes('Viewer')">
            <NavigationButton
              title="Files"
              class="icons"
              @mouseover="getPos('file')"
              @mouseout='removePopUp()'
              :routeName="routeNames.Files"
              :icon="'mdi-file-account'"
              @click="goToRoute(routeNames.Files)"
            />
          </div>
          <div class="nav-holder" v-if="this.permissions.includes('File-Access-Permission')">
            <NavigationButton
              title="Files Upload"
              class="icons"
              @mouseover="getPos('fileupload')"
              @mouseout='removePopUp()'
              :routeName="routeNames.fileupload"
              :icon="'mdi-file-send'"
              @click="goToRoute(routeNames.fileupload)"
            />
          </div>
        </div>
      </div>
      <div>
        <div class="nav-holder">
          <NavigationButton
            class="icons"
            @mouseover="getPos('userProfile')"
            @mouseout='removePopUp()'
            title="User Profile"
            :routeName="routeNames.userProfile"
            :icon="'mdi-account-circle'"
            :image="loggedInUser[17]"
            :bgColor="loggedInUser[19]"
            :initials="loggedInUser[18]"
            @click="goToRoute(routeNames.userProfile)"
          />
        </div>
        <div class="nav-holder">
          <NavigationButton
            class="icons"
            @mouseover="getPos('help')"
            @mouseout='removePopUp()'
            title="Help"
            :routeName="routeNames.help"
            :icon="'mdi-help-circle-outline'"
            @click="goToRoute(routeNames.help)"
          />
        </div>
         <div class="nav-holder">
           <NavigationButton
            title="Log Out"
            :icon="'mdi-logout'"
            @click="logout"
          />
         </div>
      </div>
       
    </div>
  </div>
  <div class="popup-modal" id="popup-modal" :style="`top: ${popupTop}; bottom: ${popupBottom}`">
    <div class="arrow-left"></div>
    <div class="popup-text">{{popupText}}</div>
  </div>
</template>

<script>
import { routeNames } from '../js/routeNames';
import NavigationButton from './NavigationButton.vue';
import { decryptData } from '../js/utils/encrypt';
import Icon from '../assets/Portal_Logo.png';
import axios from 'axios';
export default {
  name: 'Navigation',
  components: {
    NavigationButton,
  },
  computed: {
    routeNames: () => {
      return routeNames;
    },
  },
  data() {
    return {
      navOpen: false,
      permissions: [],
      loggedInUser: [],
      employees: [],
      email: '',
      popupTop: '0px',
      popupText: '',
      popupBottom: '0px',
      newIcon: Icon,
      force_change_password: false
    };
  },
  mounted() {
    //Decrypting Permissions
    let mkLocalData = localStorage.getItem('Permissions');
    if (!mkLocalData) {
      mkLocalData = 'null';
    }
    const secretPermissionKey = process.env.KEY || 'myscecretkey';
    this.permissions = decryptData(mkLocalData, secretPermissionKey);
    var encryptedEmailID = localStorage.getItem('Email');
    this.email = decryptData(encryptedEmailID, secretPermissionKey).replace(/['"]+/g, '');

    var encryptedPwdFlag = localStorage.getItem('PasswordFlag');
    this.force_change_password = decryptData(encryptedPwdFlag, secretPermissionKey);

    var loggedInUserArr = [];
    if(this.email != 'admin@scala.com'){
      axios.get(this.$hostName + '/employees').then((response) => {
        response.data.forEach((element) => {
            this.employees.push(element);
            if(element[10]==this.email){
                loggedInUserArr.push(element);
            }
        });
        for (let k = 0; k < loggedInUserArr[0].length; k++) {
          this.loggedInUser.push(loggedInUserArr[0][k]);
        }

      })
      .catch((error) => {
        console.log(error);
      }).finally(() => {
        this.loading = false;
      });
    }
  },
  methods: {
    removePopUp(){
      var removeOpacity = document.getElementById('popup-modal');
      removeOpacity.classList.remove('show');
    },
    getPos(type){
      var addOpacity = document.getElementById('popup-modal');
      addOpacity.classList.add('show');
      if(this.email == 'admin@scala.com'){
        if(type == 'user'){
          this.popupTop = '76px';
          this.popupBottom = '0px';
          this.popupText = 'Manage User';
        } else if(type == 'role'){
          this.popupTop = '125px';
          this.popupBottom = '0px';
          this.popupText = 'Manage Role';
        }else if(type == 'home'){
          this.popupTop = '175px';
          this.popupBottom = '0px';
          this.popupText = 'Home';
        } else if(type == 'info'){
          this.popupTop = '224px';
          this.popupBottom = '0px';
          this.popupText = 'My Info';
        } else if(type == 'people'){
          this.popupTop = '276px';
          this.popupBottom = '0px';
          this.popupText = 'People';
        } else if(type == 'report'){
          this.popupTop = '325px';
          this.popupBottom = '0px';
          this.popupText = 'Reports';
        } else if(type == 'file'){
          this.popupTop = '376px';
          this.popupBottom = '0px';
          this.popupText = 'Files';
        }  else if(type == 'fileupload'){
          this.popupTop = '427px';
          this.popupBottom = '0px';
          this.popupText = 'Files Upload';
        } else if(type == 'userProfile'){
          this.popupTop = 'auto';
          this.popupBottom = '125px';
          this.popupText = 'User Profile';
        } else if(type == 'help'){
          this.popupTop = 'auto';
          this.popupBottom = '74px';
          this.popupText = 'Help';
        } 
      } else {
        if(type == 'home'){
          this.popupTop = '76px';
          this.popupBottom = '0px';
          this.popupText = 'Home';
        } else if(type == 'info'){
          this.popupTop = '125px';
          this.popupBottom = '0px';
          this.popupText = 'My Info';
        } else if(type == 'people'){
          this.popupTop = '175px';
          this.popupBottom = '0px';
          this.popupText = 'People';
        } else if(type == 'file'){
          this.popupTop = '224px';
          this.popupBottom = '0px';
          this.popupText = 'Files';
        } else if(type == 'fileupload'){
          this.popupTop = '276px';
          this.popupBottom = '0px';
          this.popupText = 'Files Upload';
        }else if(type == 'userProfile'){
          this.popupTop = 'auto';
          this.popupBottom = '125px';
          this.popupText = 'User Profile';
        } else if(type == 'help'){
          this.popupTop = 'auto';
          this.popupBottom = '74px';
          this.popupText = 'Help';
        } 
      }
      
    },
    goToRoute(route) {
      if (this.navOpen) this.toggleNav();
      if (route == this.$router.currentRoute.name) return;
      this.$router.push({ name: route });
    },
    toggleNav() {
      this.navOpen = !this.navOpen;
      this.$emit('navOpen',this.navOpen);
    },
    logout() {
      localStorage.removeItem('Login');
      if (routeNames.login == this.$router.currentRoute.name) {
        return;
      }
      axios
        .post(this.$hostName + '/logout', null)
        .then(() => {
          this.$router.push({ name: routeNames.login });
        })
        .catch((error) => {
          this.errors = error.response.data.errors;
        })
        .finally(() => {
          localStorage.removeItem('token');
          localStorage.removeItem('Permissions');
          localStorage.removeItem('username');
          localStorage.removeItem('Role');
          localStorage.removeItem('EmployeeID');
          localStorage.removeItem('Email');
          localStorage.removeItem('PasswordFlag');
          localStorage.removeItem('celebrationGIF');
          location.reload();
        });
    },

  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.new-icon{
  height: 45px;
  width: 45px;
}
@media only screen and (max-width: 575.98px) {
  .popup-modal{
    display: none;
  }

}
@media (min-width: 768px) and (max-width: 991.98px) { 
  .popup-modal{
    display: none;
  }
}
.menus {
  margin-left: 36%;
  color: #7a7a7a;
  text-decoration: none;
}
.arrow-left {
  width: 0; 
  height: 0; 
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent; 
  position: absolute;
  top: 8px;
  left: -9px;
  border-right:10px solid #EE3234; 
}
.popup-modal{
  font-family: 'Montserrat';
  background: #EE3234;
  position: absolute;
  border-radius: 10px;
  width: 120px;
  left: 69px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  opacity: 0;
  
}
.show{
  z-index: 1000;
  -webkit-animation: fadein .5s; /* Safari, Chrome and Opera > 12.1 */
       -moz-animation: fadein .5s; /* Firefox < 16 */
        -ms-animation: fadein .5s; /* Internet Explorer */
         -o-animation: fadein .5s; /* Opera < 12.1 */
            animation: fadein .5s;
  animation-fill-mode: forwards;
}
@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Internet Explorer */
@-ms-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}
.popup-text{
  color: white;
}
.nav-holder:hover .popup-modal{
  opacity: 1;
}
.menus:hover {
  color: #fff;
}
.menu-icons {
  margin-left: 8%;
  font-size: x-large;
}
.navigation {
  font-family: 'Montserrat';
  z-index: 1001;
  width: 220px;
  min-width: 60px;
  height: 100%;
  top: 0;
  left: -160px;
  position: absolute;
  background-color: #d05959;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  transition: all 300ms ease-in-out;

  &.nav-opened {
    width: 360px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  }

  .navigation-shell {
    width: 200px;
    height: 98%;
    position: absolute;
    right: 0;
    top: 0;

    .navigation-button {
      width: 100%;
      height: 40px;
      // position: relative;
      float: left;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;

      &.menu-button {
        height: 60px;
      }

      &:hover {
        .copy {
          opacity: 1;
        }

        .icon {
          opacity: 1;
        }
      }

      .copy {
        // width: auto;
        flex-grow: 1;
        text-align: left;
        font-size: 14px;
        color: #fff;
        // opacity: .4;
      }

      .icon {
        width: 60px;
        font-size: 30px;
        height: 100%;
        color: #fff;

        display: flex;
        align-items: center;
        justify-content: center;
        // opacity: .4;
      }
    }
  }

  #title {
    color: #fff;
    font-size: 23px;
    // padding-left: 7px;
    // margin-top: 20%;
    // padding-bottom: 10%;
    text-align: center;
    // border-bottom: 1px solid;
  }
}
</style>
